h1,h2,h3,h4,h5,h6 {
  font-family: 'Playfair Display', serif;
  font-size: 38px;
}

h1 {
  font-size: 38px;
}

h4 {
  font-size: 25px;
  margin: 0; /* Remove margin */
}

a {
  color: white;
  text-decoration: none;
}

.home-container {
  max-width: 1200px;
  margin: auto;
  padding: 20px;
}

.home-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #fff;
}

.logo {
  width: 100px; /* Adjust as needed */
  height: auto;
}

.navigation ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.navigation li {
  cursor: pointer;
}

.home-main {
  text-align: center;
  padding: 20px;
}

.cards-container {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin: 20px 0;
}

.card-link {
  flex: 1;
  text-decoration: none;
  color: inherit;
  display: flex;
}

.card {
  flex: 1;
  background-color: #dc3545;
  color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform 0.3s;
  justify-content: center;
}

.card:hover {
  transform: scale(0.95); /* Shrinks the card on hover */
}

.card h4 {
  margin-bottom: 10px; /* Adjust as needed */
}

.card p {
  margin: 10px 0; /* Adjust as needed */
}

.card .svg-icon {
  margin-top: 10px; /* Adjust as needed */
  width: 80px; /* Adjust size as needed */
  height: auto;
  color: white; /* Change to your desired color */
  fill: currentColor; /* Use currentColor to apply the color from the parent */
}

.card button {
  background-color: #fff;
  color: #dc3545;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}

.card button:hover {
  background-color: #e2e2e2;
}

/* Responsive design for mobile devices */
@media (max-width: 768px) {
  .cards-container {
    flex-direction: column;
    align-items: center;
  }

  .card-link {
    width: 90%;
    margin-bottom: 20px;
  }

  .next-event-section {
    width: 90%;
    margin: 0 auto 20px auto; /* Center the section and add margin for spacing */
  }
}

.instagram-section {
  background-color: #333;
  color: #fff;
  padding: 20px;
  border: none;
  border-radius: 4px;
}

.spotify-section {
  background-color: #fff;
  color: #fff;
  padding: 20px;
  border: none;
  border-radius: 4px;
  margin-top: 20px;

  h4 {
    color: #333;
  }
}

.playlists-container {
  display: flex;
  justify-content: center;
  gap: 20px;
}

@media (max-width: 768px) {
  .playlists-container {
    flex-direction: column;
    align-items: center;
  }

  .playlists-container iframe {
    width: 90%;
  }
}

.svg-icons-section {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.svg-icon {
  width: 100px; /* Default width */
  height: auto;
}

@media (max-width: 768px) {
  .svg-icons-section {
    justify-content: space-around;
  }

  .svg-icon {
    width: 70px; /* Adjusted size for mobile */
  }
}

.section-container {
  display: flex;
  flex-direction: column;
  align-items: stretch; /* Ensure that children (sections) stretch to fill the container */
}

@media (min-width: 769px) { /* This applies to screens wider than 768px */
  .section-container {
    flex-direction: row;
  }

  .intro-section {
    flex: 3; /* Takes up 2 parts of the available space */
    margin: 20px 0;
    padding: 20px;
    background-color: white;
    height: 330px;
  }

  .next-event-section {
    flex: 2; /* Takes up 3 parts of the available space */
    margin: 20px 0;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative; /* This ensures that the image can be absolutely positioned within this container */
  }

  .event-image {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain; /* This will make sure the image is scaled correctly within its container */
    position: absolute; /* Image is positioned absolutely within its container */
    border: none;
    border-radius: 4px;
  }
}

@media (max-width: 768px) {
  .section-container {
    flex-direction: column;
  }

  .intro-section, .next-event-section {
    width: 100%; /* Sections take full width on smaller screens */
  }

  .event-image {
    width: 300px; /* Fixed width for mobile, if desired */
    height: 300px; /* Fixed height for mobile, if desired */
    object-fit: cover; /* Adjust as needed for mobile */
  }
}
